import Phaser from 'phaser';
import Axios from 'axios';
import Consts from '../consts';

export default class extends Phaser.Scene {
  constructor() {
    super({ key: 'SplashScene' });
  }

  preload() {
    let bgRatioWidth = this.game.config.width / Consts.BACKGROUND_IMAGE_WIDTH;
    let bgRatioHeight =
      this.game.config.height / Consts.BACKGROUND_IMAGE_HEIGHT;
    let gameCenterX = this.game.config.width * 0.5;
    let gameCenterY = this.game.config.height * 0.5;

    this.add
      .image(0, 0, Consts.BACKGROUND_IMAGE_KEY)
      .setOrigin(0, 0)
      .setScale(bgRatioWidth, bgRatioHeight);
    this.add
      .image(gameCenterX, gameCenterY - 150, Consts.GAME_TITLE_IMAGE_KEY)
      .setScale(bgRatioWidth, bgRatioHeight);

    this.add
      .image(
        80 * bgRatioWidth,
        this.game.config.height - 160 * bgRatioHeight,
        Consts.MONSTER_COVER_IMAGE_KEY
      )
      .setScale(bgRatioWidth, bgRatioHeight);

    this.add
      .image(
        this.game.config.width -
          Consts.BUTTON_SPRITE_FRAME_WIDTH * bgRatioWidth * 0.8,
        this.game.config.height -
          Consts.BUTTON_SPRITE_FRAME_HEIGHT * bgRatioHeight * 0.8,
        Consts.BUTTON_SPRITE_SHEET_KEY,
        0
      )
      .setOrigin(0)
      .setScale(bgRatioWidth * 0.8, bgRatioHeight * 0.8)
      .setInteractive()
      .on(
        'pointerdown',
        function() {
          this.scene.start('GameScene');
        },
        this
      );
    /*
    this.add.image(this.game.config.width - Consts.BUTTON_SPRITE_FRAME_WIDTH * bgRatioWidth * .8,
      this.game.config.height - Consts.BUTTON_SPRITE_FRAME_HEIGHT * 2.2 * bgRatioHeight * .8,
      Consts.BUTTON_SPRITE_SHEET_KEY, 1)
      .setOrigin(0)
      .setScale(bgRatioWidth * .8, bgRatioHeight * .8)
      .setInteractive()
      .on('pointerdown', this.openLoginForm, this);
*/
  }

  openLoginForm() {
    let loginDiv = document.getElementById('login');
    let loginBtn = document.getElementById('loginBtn');
    loginDiv.style.visibility = 'visible';
    window.scrollTo(0, 0);
    loginBtn.addEventListener('click', this.loginClicked, false);
  }

  loginClicked() {
    let loginDiv = document.getElementById('login');
    loginDiv.style.visibility = 'hidden';
    let loginBtn = document.getElementById('loginBtn');
    loginBtn.removeEventListener('click', this.loginClicked);

    let username = document.getElementById('username').value;
    let password = document.getElementById('password').value;

    // document.getElementById('username').value = '';
    // document.getElementById('password').value = '';

    console.log(`${username} , ${password}`);

    let loginUrl = 'http://54.205.68.225:8080/eddy/eddy/api/login';

    Axios.post(loginUrl, {
      username: username,
      password: password
    })
      .then(function(response) {
        window.token = response.headers['x-auth-token'];
        console.log('Axios response' + window.token);
      })
      .catch(function(error) {
        console.log('Axios response' + error);
      });
  }
}
